import '../App.css';
import { Button, Table } from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';

const List = (props) => {
    return ( 
        <div className="List">
        {      
            props.lista.length===0   
            ? <p>No hay pokem√≥n</p>
            : 
             <Table striped bordered hover>
            <thead>
              <tr>
                <th>Pokedex</th>
                <th>Nombre</th>
                <th>Tipo</th>
                <th>Region</th>
                <th></th>
                <th></th> 
              </tr>
            </thead>
              <tbody>
            {
              props.lista.map((a,index)=>
                <tr key={index}>
                    <td>{a.dex}</td>
                    <td>{a.nombre}</td>
                    <td>{a.tipo}</td>
                    <td>{a.region}</td>
                    <td><Button onClick={()=>props.eliminar(a.dex)} variant="danger">Eliminar</Button></td>
                    <td><Button onClick={()=>props.modificar(a.dex)}variant="success">Modificar</Button></td>
                </tr>
              )
            }
              </tbody>
            </Table>
          }

          </div>
     );
}
 
export default List;