import React, { Component } from 'react';
import Form from './Components/Form';
import List from './Components/List';
import Header from './Components/Header';
import firebase from './ConfFirebase.js';
import './App.css';

class App extends Component {
    constructor() {
      super();
      this.state = {
        pokemon:{
          dex:"",
          nombre:"",
          tipo:"",
          region:""
        },
        lista:[],
        desactivado:false,
      };
    }
    
    componentDidMount(){
      firebase.database().ref('Pokemon').on('value', snapshot=> {
        let pokemonLista=[];
        snapshot.forEach(row=>{
            pokemonLista.push({
              dex:row.key,
              nombre:row.val().nombre,
              tipo:row.val().tipo,
              region:row.val().region
            })
        })
        this.setState({
          ...this.state,
          lista:pokemonLista
        })
      });
    }
  
  
    guardarCambios=(e)=>{
      this.setState({ 
        ...this.state,
        pokemon:{
        ...this.state.pokemon,  
        [e.target.name]: e.target.value
        } 
        //nombre:e.target.value 
      });
    }
    
    eliminar=(id)=>{
  
      firebase.database().ref('Pokemon/' + id).set(null).then(() => {
        
      });
  
      const temporal = this.state.lista.filter(a=>a.dex!==id)
      this.setState({
        ...this.state,
        lista:temporal
      })
    }
  
    modificar=(id)=>{
      const temporal = this.state.lista.find(a=>a.dex===id);
      this.setState({
        ...this.state,
        pokemon:{
          dex:temporal.dex,
          nombre:temporal.nombre,
          tipo:temporal.tipo,
          region:temporal.region
        },
        desactivado:true
      })
    }
    enviar=(e)=>{
      e.preventDefault();
  
      const {dex, nombre, tipo, region} = this.state.pokemon;
        
      const vacios = (dex.length===0 && nombre.length===0 && tipo.length===0 && region.length===0)
      if(!vacios){
  
        firebase.database().ref('Pokemon/' + dex ).update(this.state.pokemon).then(() => {
            alert("Guardado.");
        });
  
        
        
        
        let temporal = this.state.lista;
  
        if(this.state.desactivado===true){
          temporal= temporal.filter(a=>a.dex!==dex)
        }
  
        this.setState({
          ...this.state,
          lista:[...temporal,this.state.pokemon],
            pokemon:{
            dex:"",
            nombre:"",
            tipo:"",
            region:""
          },
          desactivado:false
        })
      }
      else{
       alert("Vacio.");
        return;
      }
      
    }
    
    render() {
      
      return (
        <div className="App">
          <Header/>
          <div className="Containers">
            <Form
                enviar={this.enviar}
                guardarCambios={this.guardarCambios}
                pokemon={this.state.pokemon}
                desactivado={this.state.desactivado}
            />
            <List
              lista={this.state.lista}
              eliminar={this.eliminar}
              modificar={this.modificar}
            />
            
          </div>
        </div>
      )
    }
  }

export default App;
