import '../App.css';
import { Button } from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';

const Form = (props) => {
    return ( 
        <div className="Form">
        <form onSubmit={props.enviar}>
        <div>
          <label htmlFor="dex">Pokedex:</label>
          <input
            type="text"
            placeholder="Ejm. 999"
            onChange={props.guardarCambios}
            value={props.pokemon.dex}
            name="dex"
            disabled={props.desactivado}
          />
        </div>
        <div>
          <label htmlFor="nombre">Nombre:</label>
          <input
            type="text"
            placeholder="Ejm. Pikachu"
            onChange={props.guardarCambios}
            value={props.pokemon.nombre}
            name="nombre"
          />
        </div>
        <div>
          <label htmlFor="tipo">Tipo:</label>
          <input
            type="text"
            placeholder="Ej. Agua"
            onChange={props.guardarCambios}
            value={props.pokemon.tipo}
            name="tipo"
          />
        </div>
        <div>
          <label htmlFor="region">Region:</label>
          <input
            type="text"
            placeholder="Ej. Kanto"
            onChange={props.guardarCambios}
            value={props.pokemon.region}
            name="region"
          />
        </div>
        <button>Enviar</button>
        </form>
      </div>
    );
}
 
export default Form;